<template>
  <div class="text-group">
    <div class="input-group" :class="{'is-invalid': error}">
      <input 
        :type='type' 
        :value='value' 
        :placeholder="placeholder" 
        :name='name' 
        @input="$emit('input', $event.target.value)"
        >
      <!-- 输入框后面的按钮 -->
      <button class="btn" v-if="btnTitle" :disabled="disabled" @click="getVerifyCode">{{btnTitle}}</button>
    </div>
    <!-- 错误信息提示 -->
    <div class="invalid-feedback" v-if="error">{{ error }}</div>
  </div>
</template>

<script>
export default {
  name: 'inputGroup',
  props: {
    type: {
      type: String,
      default: 'text'
    },
    value: String,
    placeholder: String,
    name: String,
    btnTitle: String,
    disabled: Boolean,
    error: String
  },
  methods: {
    getVerifyCode() {
      this.$emit('btnClick')
    }
  },
}
</script>

<style lang="scss" scoped>
.text-group {
  margin-bottom: 20px;
}
.input-group {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: relative; 
  input {
    height: 100%;
    width: 60%;
    outline: none;
    border: none;
  }
  .btn {
    border: none;
    outline: none;
    background: #fff;
    
  }
  button[disabled] {
    color: #aaa;
  }
}

// 错误信息提示
.is-invalid {
  border: 1px solid red;
}
.invalid-feedback {
  color: red;
  padding-top: 5px;
}
</style>

