<template>
  <div class="login">
    <div class="logo">
      <img src="../assets/logo.jpg" alt="logo img">
    </div>
    <!-- 手机号 -->
    <InputGroup 
      type='number' 
      v-model="phone"
      placeholder='手机号'
      :btnTitle='btnTitle'
      :disabled='disabled'
      :error='error.phone'
      @btnClick="getVerifyCode"
      />
      <!-- 验证码 -->
    <InputGroup 
      type='number' 
      v-model="verifyCode"
      placeholder='验证码'
      :error='error.verifyCode'
      />
      <!-- 用户协议 -->
      <div class="login-des">
        <p>
          新用户登录即自动注册，表示已同意
          <span>《用户服务协议》</span>
        </p>
      </div>
      <!-- 登录按钮 -->
      <div class="login-btn">
        <button :disabled='isClick' @click="login">登录</button>
      </div>
  </div>
</template>

<script>
import InputGroup from '../components/InputGroup'
export default {
  name: 'login',
  components: {
    InputGroup
  },
  data() {
    return {
      phone: '',
      verifyCode: '',
      error: {},
      btnTitle: '获取验证码',
      disabled: false
    }
  },
  computed: {
    isClick() {
      if(!this.phone || !this.verifyCode) return true
      else return false
    }
  },
  methods: {
    // 登录
    login() {
      // 取消错误提醒
      this.error = {};
      // 发送请求
      this.$axios
        .post("/api/posts/sms_back", {
          phone: this.phone,
          code: this.verifyCode
        })
        .then(res => {
          // console.log(res);
          // 检验成功 设置登录状态并且跳转到/
          localStorage.setItem("ele_login", true);
          this.$router.push("/");
        })
        .catch(err => {
          // 返回错误信息
          this.errors = {
            code: err.response.data.msg
          };
        });
    },
    // 获取验证码
    getVerifyCode() {
      if(this.validatePhone()) {
        // 倒计时
        this.validateBtn()

        // 发送验证码请求
        this.$axios
          .post("/api/posts/sms_send", {
            tpl_id: "150261",
            key: "35da7ad1b81223d4bdc49e5f11990e16",
            phone: this.phone
          })
          .then(res => {
            // console.log(res);
          });
      }
      
    },
    validateBtn() {
      // 验证手机号
      let time = 60;
      let timer = setInterval(() => {
        if(time == 0) {
          clearInterval(timer);
          this.btnTitle = '获取验证码';
          this.disabled - false
        } else {
          this.btnTitle = time + '秒后重试';
          this.disabled = true;
          time --
        }
      }, 1000)
    },
    validatePhone() {
      if(!this.phone) {
        this.error = {
          phone: '手机号码不能为空'
        }
        return false
      } else if (!/^1[345678]\d{9}$/.test(this.phone)) {
        this.error = {
          phone: '请输入正确的手机号码'
        }
        return false
      } else {
        this.error = {}
        return true
      }
    }
  },
  created() {
    
  },
}
</script>

<style lang="scss" scoped>
.login {
  background-color: #fff;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 30px;
  .logo {
    text-align: center;
    margin-bottom: 20px;
    img {
      width: 150px;
    }
  }
  // 用户协议
  .login-des {
    color: #aaa;
    line-height: 22px;
    padding-bottom: 20px;
    span {
      color: #2395ff;
    }
  }
  // 登录按钮
  .login-btn {
    button {
      display: block;
      background-color: #4cd96f;
      line-height: 42px;
      width: 100%;
      border: none;
      border-radius: 4px;
      color: #fff;
      font-size: 16px;
    }
  }
}
</style>

